'use client'

import { useState } from 'react'
import ConversationList from '@/components/messenger/ConversationList'
import ChatWindow from '@/components/messenger/ChatWindow'
import { Conversation, Message, messages as initialMessages } from '@/lib/data'

export default function MessengerPage() {
  const [activeConversation, setActiveConversation] = useState<Conversation | null>(null)
  const [messages, setMessages] = useState<Record<string, Message[]>>(initialMessages)

  const handleConversationSelect = (conversation: Conversation) => {
    setActiveConversation(conversation)
  }

  const handleSendMessage = (content: string) => {
    if (!activeConversation) return

    const newMessage: Message = {
      id: `m${Date.now()}`,
      senderId: 'me',
      content,
      timestamp: new Date(),
      isOwn: true
    }

    setMessages(prev => ({
      ...prev,
      [activeConversation.id]: [
        ...(prev[activeConversation.id] || []),
        newMessage
      ]
    }))

    // Simulate a response after a short delay
    setTimeout(() => {
      const responseMessage: Message = {
        id: `m${Date.now() + 1}`,
        senderId: activeConversation.id,
        content: getRandomResponse(),
        timestamp: new Date(),
        isOwn: false
      }

      setMessages(prev => ({
        ...prev,
        [activeConversation.id]: [
          ...(prev[activeConversation.id] || []),
          responseMessage
        ]
      }))
    }, 1000 + Math.random() * 2000) // Random delay between 1-3 seconds
  }

  const getRandomResponse = (): string => {
    const responses = [
      "That's interesting!",
      "I see what you mean.",
      "Thanks for sharing that.",
      "Absolutely!",
      "I agree with you.",
      "That sounds great!",
      "Let me think about that.",
      "Good point!",
      "I'll get back to you on that.",
      "Sounds like a plan!"
    ]
    return responses[Math.floor(Math.random() * responses.length)]
  }

  const currentMessages = activeConversation ? messages[activeConversation.id] || [] : []

  return (
    <div className="h-screen flex bg-gray-100">
      {/* Sidebar - Conversation List */}
      <div className="hidden md:block">
        <ConversationList
          activeConversationId={activeConversation?.id || null}
          onConversationSelect={handleConversationSelect}
        />
      </div>

      {/* Mobile Sidebar - Hidden by default, can be toggled */}
      <div className="md:hidden w-full">
        {!activeConversation ? (
          <ConversationList
            activeConversationId={null}
            onConversationSelect={handleConversationSelect}
          />
        ) : (
          <div className="flex flex-col h-full">
            {/* Mobile Back Button */}
            <div className="p-4 bg-white border-b border-gray-200 md:hidden">
              <button
                onClick={() => setActiveConversation(null)}
                className="flex items-center text-blue-500 hover:text-blue-600"
              >
                <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
                Back to chats
              </button>
            </div>
            <div className="flex-1">
              <ChatWindow
                conversation={activeConversation}
                messages={currentMessages}
                onSendMessage={handleSendMessage}
              />
            </div>
          </div>
        )}
      </div>

      {/* Desktop Chat Window */}
      <div className="hidden md:flex flex-1">
        <ChatWindow
          conversation={activeConversation}
          messages={currentMessages}
          onSendMessage={handleSendMessage}
        />
      </div>
    </div>
  )
}
