'use client'

import { conversations, Conversation } from '@/lib/data'
import ConversationItem from './ConversationItem'
import { Search } from 'lucide-react'

interface ConversationListProps {
  activeConversationId: string | null;
  onConversationSelect: (conversation: Conversation) => void;
}

export default function ConversationList({ activeConversationId, onConversationSelect }: ConversationListProps) {
  return (
    <div className="w-full md:w-80 bg-white border-r border-gray-200 flex flex-col h-full">
      {/* Header */}
      <div className="p-4 border-b border-gray-200">
        <h1 className="text-xl font-bold text-gray-900 mb-3">Chats</h1>
        
        {/* Search Bar */}
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
          <input
            type="text"
            placeholder="Search Messenger"
            className="w-full pl-10 pr-4 py-2 bg-gray-100 border border-gray-200 rounded-full text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
          />
        </div>
      </div>

      {/* Conversations List */}
      <div className="flex-1 overflow-y-auto">
        {conversations.length === 0 ? (
          <div className="flex items-center justify-center h-32 text-gray-500">
            <p>No conversations available</p>
          </div>
        ) : (
          conversations.map((conversation) => (
            <ConversationItem
              key={conversation.id}
              conversation={conversation}
              isActive={activeConversationId === conversation.id}
              onClick={() => onConversationSelect(conversation)}
            />
          ))
        )}
      </div>
    </div>
  )
}
